.footer {
  width: 100vw; /* full viewport width */
  overflow-x: hidden; /* prevent horizontal scroll */
  padding-top: 40px;
  background-color: #edebeb;
  color: #000000;
  font-family: 'Segoe UI', sans-serif;
  box-sizing: border-box;
}


.footer-container {
  max-width: 1200px;
  margin: auto;
  padding: 0 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-bottom: 1px solid #ededed;
  padding-bottom: 30px;
  box-sizing: border-box;
}

.footer-section {
  flex: 1 1 250px;
  margin-bottom: 30px;
}

.footer-logo {
  height: 100px;
  margin-bottom: 12px;
}

.footer-tagline {
  font-size: 24px;
  font-weight: 600;
  color: #1e1e1e;
}

.footer-section h4 {
  margin-bottom: 12px;
  font-size: 30px;
  font-weight: bold;
  color: #000000;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section li {
  margin-bottom: 8px;
  font-size: 18px;
  color: #090909;
  font-weight: 500;
}

.footer-section a {
  color: #2d2c2c;
  text-decoration: none;
  transition: color 0.2s ease;
}

.footer-section a:hover {
  color: #545252;
}

.footer-bottom {
    border-top: 2px solid #c5c4c4;
  text-align: center;
  font-size: 13px;
  color: #888;
  padding: 15px 20px;
  background-color: #e2e0e0;
}

.footer-bottom p {
    color: #000000;
    font-size: 1rem;
    font-weight: 500;
}
.footer-bottom p a{
    color: #000000;
    text-decoration: none;
}

/* Responsive */
@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    text-align: center;
    gap: 30px;
  }

  .footer-section {
    margin-bottom: 20px;
  }

  .footer-logo {
    margin: 0 auto 12px;
  }
}
