/* === Layout === */
.booking-container {
  /* height: 100vh; */
  width: 100vw;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-family: "Segoe UI", sans-serif;
  background-color: #ffffff;
  color: #111;
  overflow-x: hidden;
}

/* === Header === */
.booking-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 150px;
  background-color: #f9f9f900;
  border-bottom: 1px solid #ddd;
}

.booking-logo {
  height: 90px;
  object-fit: contain;
}

.logout-btn {
  background-color: transparent !important;
  border: 1px solid #ccc;
  padding: 6px 14px;
  font-size: 14px;
  font-weight: 500;
  color: #333;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.logout-btn:hover {
  background-color: #eee !important;
  color: #000 !important;
}

/* === Body Split === */
.booking-body {
  flex: 1;
  display: flex;
  overflow: hidden;
  justify-content: center;
  align-items: center;
}

/* === Left Side === */
.booking-left {
  flex: 1;
  background: transparent;
  padding: 2rem;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* === Booking Box === */
.booking-box {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
  padding: 2rem;
  max-width: 500px;
  width: 100%;
  margin: auto;
  height: auto;
}

/* === Right Side (Image Carousel + Map) === */
.booking-right {
  flex: 1;
  background: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 2rem;
  text-align: center;
}

.booking-right .image-slider,
.booking-right .map-container {
  width: 100%;
  height: 300px;
  margin-bottom: 1.5rem;
}

.booking-right iframe {
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 12px;
}

.brandon {
  font-size: 15px;
  font-weight: 600;
  text-transform: uppercase;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.brandon2 {
  font-size: 45px;
  margin-top: -20px !important;
  font-weight: 800;
  /* letter-spacing: ; */
  letter-spacing: -0.12rem;
  text-transform: uppercase;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.jayesh-phone {
  font-size: 23px;
  margin-top: -20px !important;
}

.jayesh-address {
  font-size: 23px;
  margin-top: -10px !important;
}

.booking-input2 {
  width: 90%;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 1rem;
  opacity: 0;
  transform: translateY(10px);
  animation: fadeInUp 0.3s ease-in-out forwards;
}
/* .booking-input2 {
} */

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.initial-button {
  display: flex;
  flex-direction: row;
  justify-content: space-between 5px !important;
}

/* === Inputs & Buttons === */
.booking-input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 1rem;
}
.service-step-buttons {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  margin-top: 24px;
}

.booking-button {
  width: 100% !important;
  margin: 0 5px;
  padding: 10px;
  border: none;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 10px;
  font-size: 1rem;
}

.daily-hours-wrapper {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s ease, padding 0.3s ease;
  padding: 0 0;
}

.daily-hours-wrapper.open {
  max-height: 300px; /* adjust if your content is taller */
  padding: 10px 0;
}

.daily-hours-list {
  list-style-type: none;
  /* padding-left: 130px; */
  justify-items: center;
  margin: 0;
  color: #444;
  font-size: 18px;
}

.working-hours-toggle {
  margin: 8px 0;
}

/* === Button Styles === */
.btn-primary {
  background: #007bff !important;
  color: white !important;
}

.btn-green {
  background: #28a745 !important;
  color: white !important;
}

.btn-danger {
  background: #dc3545 !important;
  color: white !important;
}

.btn-secondary {
  background: #6c757d !important;
  color: white !important;
}
/* === Attractive Services === */
.service-title {
  font-size: 1.5rem !important;
  font-weight: bold !important;
  margin-bottom: 1rem !important;
  color: #111 !important;
}

.service-list {
  display: flex !important;
  flex-direction: column !important;
  gap: 10px !important;
  /* max-height: 260px; */
  overflow-y: auto !important;
  padding-right: 4px !important;
  margin-bottom: 1.5rem !important;
  border: 1px solid #eee !important;
  border-radius: 8px !important;
}

/* Individual Service Card */
.service-card {
  display: flex !important;
  align-items: flex-start !important;
  background-color: #fafafa !important;
  border: 1px solid #ddd !important;
  padding: 10px 14px !important;
  border-radius: 6px !important;
  cursor: pointer !important;
  transition: 0.2s ease !important;
  gap: 10px !important;
}

.service-card input[type="checkbox"] {
  margin-top: 4px !important;
  accent-color: #007bff !important;
}

.service-card:hover {
  background-color: #f0f0f0 !important;
}

.service-card.selected {
  border-color: #007bff !important;
  background-color: #e9f3ff !important;
}

.service-content {
  display: flex !important;
  flex-direction: column !important;
}

.service-name {
  font-size: 1.2rem !important;
  /* font-size: 0.95rem !important; */
  font-weight: 600 !important;
  margin-bottom: 2px !important;
  color: #222 !important;
}

.service-meta {
  font-size: 1.2rem !important;
  /* font-size: 0.85rem !important; */
  font-weight: 500;
  color: #767676 !important;
}

/* === Booking Summary Box === */
.summary-box {
  background: #f8f9fa !important;
  border: 1px solid #dee2e6 !important;
  border-radius: 12px !important;
  padding: 1.5rem !important;
  margin-top: 1.5rem !important;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.04) !important;
  transition: all 0.3s ease !important;
}

.summary-title {
  margin-bottom: 1.2rem !important;
  font-size: 1.4rem !important;
  font-weight: 700 !important;
  color: #333 !important;
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
}

.summary-items {
  list-style: none !important;
  padding: 0 !important;
  margin: 0 0 1rem 0 !important;
}

.summary-items li {
  display: flex !important;
  justify-content: space-between !important;
  margin-bottom: 10px !important;
  padding-bottom: 8px !important;
  border-bottom: 1px dashed #ccc !important;
  font-size: 1.2rem !important;
  color: #444 !important;
}

.summary-total {
  display: flex !important;
  justify-content: space-between !important;
  font-weight: 600 !important;
  font-size: 1.3rem !important;
  color: #000 !important;
  margin-bottom: 1.2rem !important;
}

.barber-title {
  font-size: 1.5rem !important;
  font-weight: bold !important;
  margin-bottom: 1.2rem !important;
  color: #222 !important;
}

.barber-list {
  display: flex !important;
  flex-direction: column !important;
  gap: 12px !important;
  margin-bottom: 1.5rem !important;
}

.barber-card {
  display: flex !important;
  align-items: center !important;
  background: #f8f9fa !important;
  border: 1px solid #ccc !important;
  border-radius: 8px !important;
  padding: 12px 16px !important;
  cursor: pointer !important;
  transition: 0.2s ease !important;
  gap: 12px !important;
}

.barber-card input[type="radio"] {
  accent-color: #28a745 !important;
  transform: scale(1.2) !important;
}

.barber-card.selected {
  border-color: #28a745 !important;
  background: #eaf9ee !important;
}

.barber-info {
  display: flex !important;
  flex-direction: column !important;
}

.barber-name {
  font-weight: 600 !important;
  font-size: 1.3rem !important;
  color: #111 !important;
}

.barber-role {
  font-size: 1rem !important;
  color: #555 !important;
}

.barber-buttons {
  display: flex !important;
  justify-content: space-between !important;
  gap: 10px !important;
}

.confirm-box {
  background: #f8f9fa !important;
  border: 1px solid #dee2e6 !important;
  border-radius: 12px !important;
  padding: 1.5rem !important;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.04) !important;
  margin-bottom: 2rem !important;
}

.confirm-title {
  font-size: 1.5rem !important;
  font-weight: bold !important;
  margin-bottom: 1rem !important;
  color: #333 !important;
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
}

.confirm-summary p {
  margin: 6px 0 !important;
}

.confirm-services {
  margin: 1rem 0 !important;
}

.confirm-service-list {
  list-style: none !important;
  padding: 0 !important;
  margin: 0 !important;
}

.confirm-service-list li {
  display: flex !important;
  justify-content: space-between !important;
  padding: 6px 0 !important;
  border-bottom: 1px dashed #ccc !important;
  font-size: 1.4rem !important;
  font-weight: 500;
}

.confirm-totals p {
  font-weight: 600 !important;
  margin: 6px 0 !important;
  font-size: 1.1rem !important;
}

/* === Booking History === */
.booking-history-card {
  background: #ffffff !important;
  border: 1px solid #ddd !important;
  border-left: 4px solid #007bff !important;
  border-radius: 10px !important;
  padding: 1rem !important;
  margin-bottom: 1rem !important;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.03) !important;
}

.booking-history-card p {
  margin: 6px 0 !important;
  font-size: 1.2rem !important;
}

.service-item p {
  font-size: 1.3rem;
}

.status-row {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  flex-wrap: wrap;
}

.status-box {
  padding: 5px 12px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  color: white;
  min-width: 140px;
  text-align: center;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.no-appointment {
  background-color: #d9534f; /* Bootstrap danger red */
}

.checkin-today {
  background-color: #5cb85c; /* Bootstrap success green */
}

.services-structured-container {
  width: 70vw;
  max-width: 100%;
  align-self: center;
  padding: 30px 20px;
  box-sizing: border-box;
  background: #fefefe;
  font-family: Arial, sans-serif;
}

.inrow1 {
  display: flex;
  gap: 20px;
  margin-bottom: 40px;
}

.service-category {
  background: #fff;
  padding: 16px;
  border-radius: 6px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  flex: 1;
}

.category-heading {
  font-size: 45px;
  margin-bottom: 12px;
  border-left: 4px solid #007bff;
  padding-left: 10px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  text-transform: uppercase;
  color: #333;
}

.service-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.service-list small {
  font-size: 0.55em;
  color: #706f6f;
  font-weight: 300;
  /* font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif; */
  margin-left: 3px;
}

.service-list li {
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px dashed #ccc;
  font-size: 35px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 500;
}

.service-list .second {
  display: flex;
  justify-content: space-between;
  padding: 2px 2px;
  /* border-bottom: 1px dashed #ccc; */
  font-size: 25px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 300;
}

.service-list li .pricing {
  color: #007bff;
    font-size: 2rem;
}

.service-list li.desc {
  font-size: 25px;
  font-style: italic;
  color: #706f6f;
  font-weight: 100;
  justify-content: flex-start;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  margin-left: 4px;
  border: none;
  /* font-size: 25px;
  font-style: italic;
  color: #666;
  justify-content: flex-start;
  padding-left: 10px;
  border: none; */
}

.bottom-row {
  display: flex;
  width: 100%;
  gap: 20px;
  min-height: 300px;
}

.left-half {
  width: 50%;
  display: flex;
  flex-direction: row;
  align-items: stretch;
}

.vertical-shave-text {
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  font-size: 45px;
  font-weight: bold;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  color: #222;
  padding: 10px;
  background: #fff;
  border-right: 1px solid #ddd;
  width: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
}


/* .category-heading {
  font-size: 45px;
  margin-bottom: 12px;
  border-left: 4px solid #007bff;
  padding-left: 10px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  text-transform: uppercase;
  color: #333;
} */
.vertical-shave-text span {
  border-top: 4px solid #007bff;
  padding-top: 10px;
}

.shave-column-wrapper {
  width: 85%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.shave-divider {
  border: none;
  border-top: 1px dashed #aaa;
  margin: 15px 0;
}

.right-half {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.loyalty-offer {
  margin-top: 40px;
  padding: 15px;
  background: #fffcf5;
  border: 1px solid #f5d98e;
  font-weight: 600;
  font-size: 25px;
  color: #7b5d00;
  border-radius: 5px;
  text-align: center;
}
/* ────────────── Responsive (Mobile + Tablet) ────────────── */
@media screen and (max-width: 992px) {
  .services-structured-container {
    width: 95vw;
    padding: 20px 16px;
  }

  .inrow1,
  .bottom-row {
    flex-direction: column;
    gap: 30px;
  }

  .left-half,
  .right-half {
    width: 100%;
    flex-direction: column;
    padding: 0;
  }

  .vertical-shave-text {
    writing-mode: horizontal-tb;
    transform: none;
    width: 100%;
    border-right: none;
    border-bottom: 1px solid #ddd;
    font-size: 30px;
    justify-content: flex-start;
    padding: 10px 10px;
    margin-bottom: 10px;
  }

  .vertical-shave-text span {
    border-top: none;
    border-left: 4px solid #007bff;
    padding-left: 10px;
  }

  .shave-column-wrapper {
    width: 100%;
  }

  .category-heading {
    font-size: 30px;
    border-left-width: 3px;
  }

  .service-list li {
    font-size: 16px;
  }

  .service-list li.desc {
    font-size: 13px;
  }

  .loyalty-offer {
    font-size: 15px;
  }
}

/* === Responsive: Mobile and Tablets === */
@media screen and (max-width: 768px) {
  .booking-container {
    height: auto;
    /* min-height: 100vh; */
    overflow-y: auto; /* Enable scrolling on small devices */
    /* overflow-x: hidden; ⛔ removed to allow natural layout */
    padding-bottom: 20px;
  }

  .booking-body {
    flex-direction: column;
    align-items: stretch;
    overflow: visible;
    padding: 1rem;
  }

  .booking-left,
  .booking-right {
    flex: unset;
    width: 100%;
    padding: 1rem 0;
  }

  .booking-box {
    max-width: 100%;
    padding: 1rem;
    box-shadow: none;
    border-radius: 0;
  }

  .booking-right .image-slider,
  .booking-right .map-container {
    height: 200px;
    margin-bottom: 1rem;
  }

  .booking-header {
    flex-direction: row;
    gap: 60px;
    /* justify-content: space-between !important; */
    text-align: center;
  }

  .logout-btn {
    width: 100%;
    max-width: 200px;
  }

  .vertical-shave-text span {
    border-top: none;
    border-left: 4px solid #007bff;
    padding-left: 10px;
  }

  .service-list li {
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
    border-bottom: 1px dashed #ccc;
    font-size: 30px;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    font-weight: 500;
  }

  .service-list .second {
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
    /* border-bottom: 1px dashed #ccc; */
    font-size: 25px;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    font-weight: 300;
  }

  .service-list li.desc {
    font-size: 25px;
    font-style: italic;
    color: #706f6f;
    font-weight: 100;
    justify-content: flex-start;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    margin-left: 4px;
    border: none;
    /* font-size: 25px;
  font-style: italic;
  color: #666;
  justify-content: flex-start;
  padding-left: 10px;
  border: none; */
  }

  .service-list li .pricing {
    color: #007bff;
    font-size: 1.5rem;
  }

  .loyalty-offer {
    margin-top: 40px;
    padding: 15px;
    background: #fffcf5;
    border: 1px solid #f5d98e;
    font-weight: 600;
    font-size: 25px;
    color: #7b5d00;
    border-radius: 5px;
    text-align: center;
  }
}
